import React, { Component } from "react";

// data MDBDataTable from mdbreact
import { MDBDataTable } from "mdbreact";

// component
import TopHeader from "../Components/TopHeader";

// axios
import axios from "axios";
import { Link } from "react-router-dom";
import MdTable from "../Components/MdTable";

// mdreact
import { MDBBtn } from "mdbreact";
import ModalAddHandymanToOrder from "./ModalAddHandymanToOrder";

class FixesNotSet extends Component {
  state = {
    data: [],
    names: [],
    lodintable: true,
    rowdata: {},
  };

  handleShow = (dat) => {
    this.setState({
      show: true,
      rowdata: dat
    });
  };

  async componentDidMount() {
    await axios
      .get(
        "http://sal7lyy-001-site1.gtempurl.com/api/Orders/GetAllOrdersByStep?OrderSteps=28e5d007-fdce-4311-156a-08d9821c839c"
      )
      .then((res) => {
        // console.log(res);
        if (res.status === 200) {
          this.setState({
            lodintable: false,
          });
        }
        var newData = res.data.data.reverse();
        newData.map((dat, index) => {
          // dat.option = (
          //   <div className="option-parent">
          //     {/* <Link to="/" className="tableOption op-delete">
          //       <i className="fi-rr-trash"></i>
          //     </Link> */}
          //     {/* <Link to="/" className="tableOption op-order">
          //       <i className="fi-rr-apps"></i>
          //     </Link>
          //     <Link to="/" className="tableOption op-edit">
          //       <i className="fi-rr-edit"></i>
          //     </Link> */}
          //   </div>
          // );
          dat.technicals = (
            <div className="option-parent">
              <MDBBtn
                className="btnOpenModal"
                size="sm"
                onClick={()=>this.handleShow(dat)}
              >
                <i className="fi-rr-pencil"></i>
                <span> </span>
              </MDBBtn>
            </div>
          );

          if(dat.customer != null){
            dat.fullName = dat.customer.fullName;
          }

          if(dat.employee != null){
            dat.employyrigister = dat.employee.fullName;
          }

          if(dat.service != null){
            dat.service = dat.service.name;
          }

          if(dat.step != null){
            dat.orderstep = dat.step.name;
          }

          if(dat.orderType != null){
            dat.ordersource = dat.orderType.name;
          }

          if(dat.customer != null){
            dat.customertype = dat.customer.customerType.name;
          }

          var datem = dat.dateCreated.split("T")[0];
          dat.dateC = datem;


        });
        this.setState({
          data: newData,
        });
      });

  }

  render() {
    const datatable = {
      columns: [
        { label: "", field: "fullName" },
        { label: "", field: "technicals" },
        { label: "", field: "service" },
        { label: "", field: "price" },
        { label: "", field: "ordersource" },
        { label: " ", field: "customertype" },
        { label: "", field: "employyrigister" },
        { label: "", field: "orderstep" },
        { label: " ", field: "dateC" },
        // { label: "", field: "option" },
      ],
      rows: this.state.data,
    };

    const handleClose = () => {
      this.setState({
        show: false,
      });
    };

    const handelChange = (e) => {
      this.setState({
        [e.target.name]: e.target.value,
      });
    };
    return (
      <div>
        
        <h5 class="mb-3 text-center font-weight-bold h4">  </h5>
        <div
          className={this.state.lodintable ? "seeloading" : "seelodingdnone"}
        >
          <div class="spinner-grow text-primary" role="status">
            <span class="sr-only">Loading...</span>
          </div>
          <div> </div>
        </div>
        <MdTable datatable={datatable} />

        <ModalAddHandymanToOrder
          show={this.state.show}
          handleShow={this.handleShow}
          handleClose={handleClose}
          rowdata = {this.state.rowdata}
          // handelChange={handelChange}
        />
      </div>
    );
  }
}

export default FixesNotSet;
