import React, { Component } from "react";

// data MDBDataTable from mdbreact
import { MDBDataTable } from "mdbreact";

// component
import TopHeader from "../Components/TopHeader";

// axios
import axios from "axios";
import { Link } from "react-router-dom";
import MdTable from "../Components/MdTable";
import { OverlayTrigger, Tooltip } from "react-bootstrap";

// mdreact
import { MDBBtn } from "mdbreact";
import ModalAddHandymanToOrder from "./ModalAddHandymanToOrder";
// import FixesNotSet from "../Components/FixesNotSet";

class FixesCancel extends Component {
  state = {
    data: [],
    names: [],
    lodintable: true,
  };

  handleShow = () => {
    this.setState({
      show: true,
    });
  };

  async componentDidMount() {
    // console.log("hi");
    // var name= {};
    await axios
      .get(
        "http://sal7lyy-001-site1.gtempurl.com/api/Orders/GetAllOrdersByStep?OrderSteps=91afb632-8f3d-42d9-156e-08d9821c839c"
      )
      .then((res) => {
        // console.log(res);
        if (res.status === 200) {
          this.setState({
            lodintable: false,
          });
        }
        var newData = res.data.data;
        newData.map((dat, index) => {
          // dat.option = (
          //   <div className="option-parent">
          //     <OverlayTrigger
          //       overlay={
          //         <Tooltip id="tooltip-disabled" className="tooltipcalss">
          //            
          //         </Tooltip>
          //       }
          //     >
          //       <button
          //         onClick={() => this.finishOrder(dat)}
          //         className="tableOption op-delete"
          //       >
          //         <i className="fi-rr-checkbox"></i>
          //       </button>
          //     </OverlayTrigger>

          //     <OverlayTrigger
          //       overlay={
          //         <Tooltip id="tooltip-disabled" className="tooltipcalss">
          //            
          //         </Tooltip>
          //       }
          //     >
          //       <Link
          //         to={"/edit-order-info/" + dat.id}
          //         className="tableOption op-edit"
          //       >
          //         <i className="fi-rr-edit"></i>
          //       </Link>
          //     </OverlayTrigger>
          //   </div>
          // );
          dat.technicals = (
            <div className="option-parent">
              <MDBBtn
                className="btnOpenModal"
                size="sm"
                onClick={this.handleShow}
              >
                <i className="fi-rr-pencil"></i>
                <span> </span>
              </MDBBtn>
            </div>
          );
          if (dat.customer != null) {
            dat.fullName = dat.customer.fullName;
          }

          if (dat.employee != null) {
            dat.employyrigister = dat.employee.fullName;
          }

          if (dat.service != null) {
            dat.service = dat.service.name;
          }

          if (dat.step != null) {
            dat.orderstep = dat.step.name;
          }

          if (dat.handyMan != null) {
            dat.handymen = dat.handyMan.fullName;
          }

          if (dat.orderType != null) {
            dat.ordersource = dat.orderType.name;
          }

          if (dat.customer != null) {
            dat.customertype = dat.customer.customerType.name;
          }

          var datem = dat.dateCreated.split("T")[0];
          dat.dateC = datem;
        });
        this.setState({
          data: newData,
        });
      });
  }

  finishOrder = (dat) => {
    // console.log(dat.orderStepId);
    dat.orderStepId = "75a5b650-33ea-4f81-b4fc-08d976563b97";
    axios
      .post(
        "http://sal7lyy-001-site1.gtempurl.com/api/Orders/UpdateOrder/" +
          dat.id,
        dat
      )
      .then((res) => {
        if (res.status === 200) {
          window.location.reload();
        }
        // console.log(res.data.data);
      });
  };

  render() {
    const datatable = {
      columns: [
        { label: "", field: "fullName" },
        { label: "", field: "handymen" },
        { label: "", field: "service" },
        { label: "", field: "price" },
        { label: "", field: "orderstep" },
        { label: " ", field: "customertype" },
        { label: "", field: "ordersource" },
        { label: "", field: "employyrigister" },
        { label: " ", field: "dateC" },
        // { label: "", field: "option" },
      ],
      rows: this.state.data,
    };

    const handleClose = () => {
      this.setState({
        show: false,
      });
    };

    const handelChange = (e) => {
      this.setState({
        [e.target.name]: e.target.value,
      });
    };

    const submitForm = (e) => {
      e.preventDefault();
    };

    return (
      <div>
        <h5 class="mb-3 text-center font-weight-bold h4"></h5>

        <div
          className={this.state.lodintable ? "seeloading" : "seelodingdnone"}
        >
          <div class="spinner-grow text-primary" role="status">
            <span class="sr-only">Loading...</span>
          </div>
          <div> </div>
        </div>
        <MdTable datatable={datatable} />

        <ModalAddHandymanToOrder
          show={this.state.show}
          handleShow={this.handleShow}
          handleClose={handleClose}
          submitForm={submitForm}
        />
      </div>
    );
  }
}

export default FixesCancel;
