import React, { Component } from "react";

import axios from "axios";

import MdTable from "../Components/MdTable";

// react-router-dom
import { Link } from "react-router-dom";

// react-toastify
import { toast } from "react-toastify";

export default class AllOrdersForCustomer extends Component {
  state = {
    data:[],
    lodintable: false,
  };

  async componentDidMount() {
    const customerId = this.props.match.params.id;
    await axios
      .get(
        "http://sal7lyy-001-site1.gtempurl.com/api/Orders/GetAllOrdersByCustomer/" +
          customerId
      )
      .then((res) => {
        // console.log(res.data.data);
        this.setState({
          data: res.data.data
        })
      });


      axios
      .get("http://sal7lyy-001-site1.gtempurl.com/api/Customer/GetCustomer/" + customerId)
      .then((res) => {
        var newDa = this.state.data;
        newDa.map((da) => {
          da.fullName = res.data.data.fullName;
        });
        this.setState({
          data: newDa,
        });
      });
  }

  render() {
    const datatable = {
      columns: [
        { label: "", field: "fullName" },
        { label: "", field: "technicals" },
        { label: "", field: "service" },
        { label: "", field: "price" },
        { label: "", field: "orderstep" },
        { label: " ", field: "dateCreated" },
      ],
      rows: this.state.data,
    };
    return (
      <div>
        <div className="main-content">
          <div className="dashboard-header">
            <h5></h5>
            <div className="add-new-user">
              <Link to="/add-new-customer">
                <i className="fas fa-plus"></i>
                <span> </span>
              </Link>
            </div>
          </div>
          <div className="orders-table">
            <div className="orderTable">
              <div
                className={
                  this.state.lodintable ? "seeloading" : "seelodingdnone"
                }
              >
                <div className="spinner-grow text-primary" role="status">
                  <span className="sr-only">Loading...</span>
                </div>
                <div> </div>
              </div>
              <MdTable datatable={datatable} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
